import React from 'react';
import MobileNavigation from './MobileNavigation'
import NavLinks from './NavLinks';

const NavBar = (props) => {
    return (
        <div className="NavBar">
            <nav className="navigation">
                <NavLinks {...props} />
            </nav>
            <MobileNavigation {...props} />
        </div>
    )
}

export default NavBar;