import { useReducer } from "react"
import { postActions, userActions } from "../../actions"

/**
 * @typedef {Object} feedState
 * @property {boolean} loggedIn
 * @property {import("../../../../server-express-mysql/controllers/GroupController").group[]} groups
 * @property {import("../../../../server-express-mysql/controllers/PostController").post[]} posts
 * @property {import("../../../../server-express-mysql/controllers/UserContoller").user[]} users
 */

/** 
 * @type {feedState}
 */
const mainPageState = {
  groupsView: {
    loading: true,
    groups: [],
  },
  feedView: {
    loading: true,
    posts: [],
  },
  peopleView: {
    users: {
      loading: true,
      people: []
    },
    relationships: {
      loading: true,
      friends: [],
    },
    requests: {
      loading: true,
      incoming: [],
      outgoing: [],
    }
  },
  error: null
}

/**
 * 
 * @param {feedState} state 
 * @param {{ type: string, payload: any }} action 
 * @returns 
 */
const feedReducer = (state, action) => {
  switch (action.type) {
    case 'GET_GROUPS': return { ...state, groups: action.payload }
    case 'GET_POSTS': return { ...state, posts: action.payload }
    case 'GET_USERS_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        users: {
          loading: false,
          people: action.payload
        }
      }
    }
    case 'GET_FRIENDS_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        relationships: {
          loading: false,
          friends: action.payload
        }
      }
    }
    case 'GET_REQUESTS_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        requests: {
          loading: false,
          ...action.payload
        }
      }
    }
    case 'SEND_REQUEST_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        users: {
          ...state.peopleView.users,
          people: state.peopleView.users.people.filter(user => user.userId !== action.payload)
        },
        requests: {
          ...state.peopleView.requests,
          outgoing: [...state.peopleView.requests.outgoing, state.peopleView.users.people.find(user => user.userId === action.payload)]
        }
      }
    }
    case 'ACCEPT_REQUEST_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        relationships: {
          ...state.peopleView.users,
          friends: [
            ...state.peopleView.relationships.friends,
            (!state.peopleView.relationships.friends.find(user => user.userId === action.payload))
            && state.peopleView.requests.incoming.find(user => user.userId === action.payload)
          ]
        },
        requests: {
          ...state.peopleView.requests,
          incoming: state.peopleView.requests.incoming.filter(user => user.userId !== action.payload)
        }
      }
    }
    case 'REJECT_REQUEST_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        requests: {
          ...state.peopleView.requests,
          incoming: state.peopleView.requests.incoming.filter(user => user.userId !== action.payload)
        }
      }
    }
    case 'CONFIRM_REQUEST_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        relationships: {
          ...state.peopleView.users,
          friends: [
            ...state.peopleView.users.people,
            (action.payload.status === 'accepted' & !state.peopleView.relationships.friends.find(user => user.userId === action.payload))
            && state.peopleView.requests.incoming.find(user => user.userId === action.payload.userId)
          ]
        },
        requests: {
          ...state.peopleView.requests,
          incoming: state.peopleView.requests.incoming.filter(user => user.userId !== action.payload.userId)
        }
      }
    }
    case 'DELETE_REQUEST_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        people: {
          ...state.peopleView.users,
          people: [...state.peopleView.users.people, state.peopleView.requests.outgoing.find(user => user.userId === action.payload)]
        },
        requests: {
          ...state.peopleView.requests,
          outgoing: state.peopleView.requests.outgoing.filter(user => user.userId !== action.payload)
        }
      }
    }
    case 'DELETE_FRIEND_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        users: {
          ...state.peopleView.users,
          people: [...state.peopleView.users.people, state.peopleView.relationships.friends.find(user => user.userId === action.payload)]
        },
        relationships: {
          ...state.peopleView.relationships,
          friends: state.peopleView.relationships.friends.filter(user => user.userId !== action.payload)
        }
      }
    }
    case 'BLOCK_USER_SUCCESS': return {
      ...state,
      peopleView: {
        ...state.peopleView,
        users: {
          ...state.peopleView.users,
          people: state.peopleView.users.people.filter(user => user.userId !== action.payload)
        },
        relationships: {
          ...state.peopleView.relationships,
          friends: state.peopleView.relationships.friends.filter(user => user.userId !== action.payload)
        },
        requests: {
          ...state.peopleView.requests,
          incoming: state.peopleView.requests.incoming.filter(user => user.userId !== action.payload),
          outgoing: state.peopleView.requests.outgoing.filter(user => user.userId !== action.payload)
        }
      }
    }

    case 'CREATE_POST_SUCCESS': return {
      ...state,
      feedView: {
        posts: [...state.feedView.posts, action.payload]
      }
    }
    case "GET_POSTS": return {
      ...state,
      feedView: {
        ...state.feedView,
        loading: true,
      }
    }
    case 'GET_POSTS_SUCCESS':
      console.log(action)
      return {
        ...state,
        feedView: {
          loading: false,
          posts: action.payload.reverse(),
        }
      }

    case "ERROR": return {
      ...state,
      groupsView: action.payload.view === 'groups' ? { ...state.groupsView, loading: false } : { ...state.groupsView },
      feedView: action.payload.view === 'feed' ? { ...state.feedView, loading: false } : { ...state.feedView },
      peopleView: action.payload.view === 'people' ? {
        users: { ...state.peopleView.users, loading: false },
        relationships: { ...state.peopleView.relationships, loading: false },
        requests: { ...state.peopleView.requests, loading: false },
      } : { ...state.peopleView },
      error: action.payload.error,
    }
    default: return { ...state }
  }
}

const useMainPageReducer = () => {
  const [state, dispatch] = useReducer(feedReducer, mainPageState);
  const {
    getUsers, getRelationships, getRequests, sendRequest, deleteRequest,
    acceptRequest, rejectRequest, confirmRequest, deleteFriend, blockUser
  } = userActions;
  const { getPosts, createPost, editPost, deletePost, } = postActions;

  const actions = {
    onGetUsers: () => {
      try {
        getUsers().then((res) => {
          if (!res) throw new Error('No users found');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onGetRelationships: () => {
      try {
        getRelationships().then((res) => {
          if (!res) throw new Error('No relationships found');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onGetRequests: () => {
      try {
        getRequests().then((res) => {
          if (!res) throw new Error('No requests found');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onSendRequest: (addresseeId) => {
      try {
        sendRequest(addresseeId).then((res) => {
          if (!res) throw new Error('Failed to send request');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onAcceptRequest: (addresseeId) => {
      try {
        acceptRequest(addresseeId).then((res) => {
          if (!res) throw new Error('Failed to accept request');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onRejectRequest: (addresseeId) => {
      try {
        rejectRequest(addresseeId).then((res) => {
          if (!res) throw new Error('Failed to reject request');
          dispatch();
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onConfirmRequest: (addresseeId) => {
      try {
        confirmRequest(addresseeId).then((res) => {
          if (!res) throw new Error('Failed to confirm request');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onDeleteRequest: (addresseeId) => {
      try {
        deleteRequest(addresseeId).then((res) => {
          if (!res) throw new Error('Failed to delete request');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onDeleteFrienship: (addresseeId) => {
      try {
        deleteFriend(addresseeId).then((res) => {
          if (!res) throw new Error('Failed to delete friend');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },
    onBlockUser: (addresseeId) => {
      try {
        blockUser(addresseeId).then((res) => {
          if (!res) throw new Error('Failed to block user');
          dispatch(res);
        })
      } catch (error) {
        console.error(error);
        dispatch({ type: 'ERROR', payload: { error: error.message, view: 'people' } })
      }
    },

    onGetPosts: () => {
      dispatch({ type: 'GET_POSTS' });
      getPosts().then(res => {
        try {
          if (!res) throw new Error('No posts found');
          dispatch(res);
        } catch (error) {
          console.error(error);
          dispatch({ type: 'ERROR', payload: { error: error.message, view: 'feed' } })
        }
      })
    },
    onCreatePost: (postData) => (
      createPost(postData).then(res => {
        try {
          if (!res) throw new Error('Failed to create post');
          dispatch(res);
        } catch (error) {
          console.error(error);
          dispatch({ type: 'ERROR', payload: { error: error.message, view: 'feed' } })
        }
      })
    ),
    onEditPost: (postEdit) => (
      editPost(postEdit).then(res => {
        try {
          if (!res) throw new Error('Failed to edit post');
          dispatch(res);
        } catch (error) {
          console.error(error);
          dispatch({ type: 'ERROR', payload: { error: error.message, view: 'feed' } })
        }
      })
    ),
    onDeletePost: (postId) => (
      deletePost(postId).then(res => {
        try {
          if (!res) throw new Error('Failed to delete post');
          dispatch(res);
        } catch (error) {
          console.error(error);
          dispatch({ type: 'ERROR', payload: { error: error.message, view: 'feed' } })
        }
      })
    ),
  }

  return { ...state, actions }
};

export default useMainPageReducer;
