/**
 * Desc: Post component that displays a post and its details
 * Props:
 * - isLoggedIn: boolean
 * - loggedInUser: object
 * - post: object
 * - onUpdateVotes: function
 * - onDeletePost: function
 * - onGetProfile: function
 * - onAddFriend: function
 * - onGetComments: function
 * - onAddComment: function
 * - onDeleteComment: function
 * - onEditComment: function
 * - onGetCommentReplies: function
 */
// Dependencies
import React, { useEffect, useReducer, useState } from 'react';
import { Link } from 'react-router-dom';
// Components
import CommentView from './Comments';
import EditPostForm from '../../forms/editPostForm/editPostForm';
import './index.css';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser, faUsers, faUserPlus, faEdit, faTrashAlt, faThumbsUp, faThumbsDown, faCommentDots } from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';

const Post = (props) => {
  const { loggedInUser, onUpdateVotes, onDeletePost, onGetProfile, onAddFriend } = props;

  const [state, dispatch] = useReducer((state, action) => {
    switch (action.type) {
      case "OPEN_POST_MODAL":
        return { ...state, edit: { ...state.edit, display: true } };
      case "CLOSE_POST_MODAL":
        return { ...state, edit: { ...state.edit, display: false } };
      default:
        return { ...state };
    }
  }, {
    isPostAuthor: props.post.author.userId === loggedInUser.userId,
    post: props.post,
    edit: {
      loading: false,
      display: false,
    },
    comments: {
      loading: true,
      display: false,
      list: [],
    }
  });

  library.add(faUser, faUsers, faUserPlus, faEdit, faTrashAlt, faThumbsUp, faThumbsDown, faCommentDots);

  return (
    <div
      className='main-post-wrapper'
    >
      <div className='post-detail'>
        {/* User Info/Controls */}
        <div className='userName'>
          {/* User Info */}
          <div className="user-profile">
            <h3>{state.post.author.userName}</h3>
          </div>

          {/* User Controls */}
          <div className='control-group'>
            {/* To Profile Icon */}
            <div className="svg-icons">
              {!state.isPostAuthor && (
                <Link onClick={() => onGetProfile(state.author.userId)} to={`/profile`}>
                  <FontAwesomeIcon className="user-icon" icon="user" />
                </Link>
              )}
            </div>
            {/* Add Friend Icon */}
            <div className="svg-icons">
              {!state.isPostAuthor && (
                <FontAwesomeIcon
                  icon="user-plus"
                  className="user-plus-icon"
                  onClick={() => onAddFriend(state.post.author.userId)}
                />
              )}
            </div>
            {/* Edit Icon */}
            <div className="svg-icons">
              {state.isPostAuthor &&
                <FontAwesomeIcon
                  icon="edit"
                  onClick={() => dispatch({ type: "OPEN_POST_MODAL" })}
                />
              }
            </div>
            {/* Delete Icon */}
            <div className="svg-icons">
              {((loggedInUser.admin === 1) || state.isPostAuthor) &&
                <FontAwesomeIcon
                  icon="trash-alt"
                  className="trash-icon"
                  onClick={() => onDeletePost(state.postId)}
                />
              }
            </div>
          </div>
        </div>

        {/* Post Body */}
        <div className='post-body-wrapper'>
          <div className="post-body" >
            <h4>{state.post.title}</h4>
            <p>{state.post.body}</p>
          </div>

          <div style={state.edit.display ? { display: 'block' } : { display: 'none' }}>
            <EditPostForm {...props} setEditModal={() => dispatch({ type: "CLOSE_POST_MODAL" })} post={state.post} />
          </div>

          {/* Private Flag */}
          <div>
            {state.isPostAuthor && (
              <p>Private: {state.private === 0 ? 'false' : 'true'}</p>
            )}
          </div>
        </div>

        {/* Interactions */}
        <div className='post-vote'>
          {/* Reaction Icons/Counts */}
          <div className="votes">
            {/* Likes Icon/Count */}
            <div className="thumbs-up">
              <FontAwesomeIcon
                icon="thumbs-up"
                onClick={() => onUpdateVotes('likes', state.likes, state.postId)}
              />
              <span>{state.likes}</span>
            </div>
            {/* Dislikes Icon/Counts */}
            <div className="thumbs-down">
              <FontAwesomeIcon
                icon="thumbs-down"
                className="thumbs-down-icon"
                onClick={() => onUpdateVotes('dislikes', state.dislikes, state.postId)}
              />
              <span className="vote-count">{state.dislikes}</span>
            </div>
          </div>

          {/* Comment Icon/Count */}
          <div>
            <FontAwesomeIcon
              className="main-post-comment-icon"
              icon="comment-dots"
              onClick={() => dispatch({ type: "OPEN_POST_MODAL" })}
            />
            <span>{state.comments?.list?.length}</span>
          </div>
        </div>

        {/* Comment Section */}
        <CommentView
          {...props}
          post={state.post}
          commentView={state.comments.display}
          postAuthor={state.post.author.userId}
        />
      </div>
    </div >
  )
}

export default Post;