import React, { useEffect, useState } from 'react';

import './index.css';
import LoadingPanel from '../../global/LoadingPanel';

const FriendsView = ({
  users, relationships, requests, onSendRequest, onAcceptRequest, onRejectRequest,
  onConfirmRequest, onDeleteRequest, onDeleteFrienship, onBlockUser,
}) => {
  const [view, setView] = useState('people');
  const [friendCount, setfriendCount] = useState(0);
  const [requestCount, setRequestcount] = useState(0);

  useEffect(() => {
    if (!relationships.loading) setfriendCount(relationships.friends?.length ?? 0);
    if (!requests.loading) setRequestcount((requests.outgoing?.length ?? 0) + (requests.incoming?.length ?? 0));
  }, [relationships, requests]);

  return (
    <div className='friendList'>
      <div className="friends-container">
        <div className="tab">
          <button className="tablinks" onClick={() => setView('people')}>
            <h3>People you may know</h3>
          </button>
          <button className="tablinks" onClick={() => setView('friends')}>
            <h3>Friend List <span>({friendCount})</span></h3>
          </button>
          <button className="tablinks" onClick={() => setView('requests')}>
            <h3>Friend Requests <span>({requestCount})</span></h3>
          </button>
        </div>

        {/* people */}
        <div className="people" style={view === 'people' ? { display: 'block', position: 'relative', height: '100%' } : { display: 'none' }}>
          <h3 style={{ display: 'flex', justifyContent: 'space-around' }}>
            <span style={{ width: '80%' }}>People you may know</span>
            {users?.loading && <LoadingPanel />}
          </h3>
          {!users?.loading && users.people?.map(user => (
            <div key={user.userId}>
              <h4 style={{ display: 'inline-block', marginRight: '20px' }}>{user.userName}</h4>
              <button onClick={() => onSendRequest(user.userId)}>Add Friend</button>
              <button onClick={() => onBlockUser(user.userId)}>Block</button>
            </div>
          ))}
        </div>

        {/* friends */}
        <div className="friends-pending" style={view === 'friends' ? { display: 'block' } : { display: 'none' }}>
          <div className="friends-list">
            <h2 style={{ display: 'flex', justifyContent: 'space-around' }}>
              <span style={{ width: '80%' }}>Friends</span>
              {relationships?.loading && <LoadingPanel />}
            </h2>
            {!relationships?.loading && (relationships?.friends.length > 0 ? relationships?.friends.map(user => (
              <div key={user.userId} className="friends">
                <h4>{user.userName}</h4>
                <button onClick={() => onDeleteFrienship(user.userId)}>Remove</button>
                <button onClick={() => onBlockUser(user.userId)}>Block</button>
              </div>
            )) : <span style={{ display: 'block', fontSize: "small" }}>No Friends Added</span>)}
          </div>
        </div>

        {/* requests */}
        <div className="incoming-request-wrapper" style={view === 'requests' ? { display: 'block' } : { display: 'none' }}>
          <h3>
            <span>Outgoing requests</span>
            {requests?.loading && <LoadingPanel />}
          </h3>
          {!requests.loading && requests.outgoing?.map(user => (
            <div key={user.userId} className="friends-request-notification">
              <div>
                <h4>{user.userName}</h4>
                <p>Request Has Been Sent</p>
              </div>
              <button onClick={() => onDeleteRequest(user.userId)}>Cancel</button>
              <button onClick={() => onBlockUser(user.userId)}>Block</button>
            </div>
          ))}

          <h3>
            <span>Incoming requests</span>
            {users?.loading && <LoadingPanel />}
          </h3>
          {!requests.loading && requests.incoming?.filter(user => user.status === 'pending').map(user => (
            <div key={user.userId} className="incoming-request">
              <div>
                <h3>{user.userName}</h3>
                <p>Has sent you a friend request</p>
              </div>

              <div className="incoming-request-btn">
                <button className="incoming-request-accept-btn" onClick={() => onAcceptRequest(user.userId)}>
                  Accept
                </button>
                <button className="incoming-request-deny-btn" onClick={() => onRejectRequest(user.userId)}>
                  Deny
                </button>
                <button onClick={() => onBlockUser(user.userId)}>Block</button>
              </div>
            </div>
          ))}

          {!requests.loading && requests.incoming?.filter(user => user.status === 'accepted').map(user => (
            <div key={user.userId}>
              <div>
                <h4>{user.userName}</h4>
                <p>Has accepted your request</p>
              </div>
              <button onClick={() => onConfirmRequest(user.userId)}>Okay</button>
            </div>
          ))}

          {!requests.loading && requests.incoming?.filter(user => user.status === 'rejected').map(user => (
            <div key={user.userId}>
              <div>
                <h4>{user.userName}</h4>
                <p>Has rejected your request</p>
              </div>
              <button onClick={() => onConfirmRequest(user.userId)}>Okay</button>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}

export default FriendsView