//css
import './index.css'

import React, { useEffect } from 'react';

import GroupView from '../../components/mainPage/groupsView/groupView';
import PeopleView from '../../components/mainPage/PeopleView';

// Recuder
import useMainPageReducer from './reducer';
import FeedView from '../../components/mainPage/FeedView';


const MainPage = (props) => {
  const { loggedInUser, isLoggedIn } = props;
  const { actions, peopleView, feedView } = useMainPageReducer();


  useEffect(() => {
    actions.onGetUsers();
    actions.onGetRelationships();
    actions.onGetRequests();

    actions.onGetPosts();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className='mainPage-container'>
      <GroupView {...props} />

      <FeedView {...{ loggedInUser, isLoggedIn, ...feedView, ...actions }} />

      <PeopleView {...{ ...peopleView, ...actions }} />
    </div>
  )
}

export default MainPage;
