// Dependencies
import { useState } from 'react';
import { useNavigate } from 'react-router-dom-v5-compat';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as yup from 'yup';
// Actions
import { utilActions } from '../../actions';
// CSS
import './index.css';
// Icon Components
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUserCircle, faEnvelope, faUser, faLock } from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';

library.add(faUserCircle, faEnvelope, faUser, faLock);

const validate = yup.object({
  email: yup.string().email('Must Be A Valid Email Address').required('An Email Is Needed To'),
  password: yup.string().required().min(8, 'Your Password Must Be A Minimum Of 8 Characters')
})

const Login = () => {
  const navigate = useNavigate(),
    [loading, setLoading] = useState(false),
    onLogin = (values) => {
      setLoading(true);
      try {
        utilActions.login(values).then((res) => {
          if (!res) throw new Error('Signup Failed');
          navigate('/', { state: { user: res } });
        })
      } catch (error) {
        setLoading(false);
        console.error(error);
      }
    },
    loadingView = (
      <div className='loading-screen'>
        <div className='loader'></div>
      </div>
    )

  return (
    <>
      {loading && loadingView}
      <div className="login-container">
        <div className="login-text">
          <h1>Log In</h1>
        </div>

        <Formik
          initialValues={{
            email: '',
            password: '',
          }}
          validationSchema={validate}
          onSubmit={(values, actions) => {
            onLogin(values);
          }}
        >
          <Form className="login-form-wrapper">
            <div className="login-email">
              {/* <label htmlFor='email'>Email: </label> */}
              <Field
                id='email'
                name='email'
                type='email'
                placeholder='Email'
              />
              <FontAwesomeIcon className="icon-login" icon="envelope" />
              <ErrorMessage name="email" component="div" />
            </div>

            <div className="login-password">
              {/* <label htmlFor='password'>Password</label> */}
              <Field
                id='password'
                name='password'
                type='password'
                placeholder='Password'
              />
              <FontAwesomeIcon className="icon-login" icon="lock" />
              <ErrorMessage name="password" component="div" />
            </div>

            <div className="login-btn">
              <button type='submit'>Login</button>
              <button type='button' onClick={() => navigate('/signup')}>Sign Up</button>
            </div>
          </Form>
        </Formik>
      </div>
    </>
  )
}

export default Login