
// Dependencies
import React, { useEffect, useState } from 'react';
// Icons
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faUser,
  faUserPlus,
  faEdit,
  faTrashAlt,
  faThumbsUp,
  faThumbsDown,
  faCommentDots
} from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core'

import './index.css';
import { Link } from 'react-router-dom-v5-compat';

const Comment = ({ comment, loggedInUser, onDeleteComment, onGetProfile }) => {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const isPostAuthor = comment.author.userId === loggedInUser.userId;
    const isCommentAuthor = comment.author.userId === loggedInUser.userId;
    const isAdmin = loggedInUser.admin === 1;
    setIsVisible((isCommentAuthor || isPostAuthor || isAdmin));
  }, [loggedInUser, comment])

  library.add(faUser, faUserPlus, faEdit, faTrashAlt, faThumbsUp, faThumbsDown, faCommentDots);

  return (
    <div key={comment.id} className="comment-view-container">
      {/* Comment Body */}
      <div className="comment-body">
        <p>{comment.body} -</p>
        {/* TODO: implement get profile action in comment component */}
        <Link className="link-profile" onClick={() => onGetProfile(comment.author.userId)} to={`/profile`}>
          <h5>{comment.author.userName}</h5>
        </Link>
      </div>

      {/* Interaction Panel */}
      <div className="comment-vote-container">
        {/* Likes Icon/Count */}
        <div className="thumbs-up">
          <FontAwesomeIcon
            icon="thumbs-up"
            // TODO: Implement reaction action in comment component
            onClick={() => { }}
          />
          <span>{comment.likes}</span>
        </div>

        {/* Dislikes Icon/Count */}
        <div className="thumbs-down">
          <FontAwesomeIcon
            className="thumbs-down-icon" icon="thumbs-down"
            onClick={() => { }}
          />
          <div className="vote-count">{comment.dislikes}</div>
        </div>

        {/* Delete Icon */}
        <div>
          <button
            className="comment-delete-btn"
            style={isVisible ? { display: 'block' } : { display: 'none' }}
            onClick={() => isVisible ? onDeleteComment({ commentId: comment.commentId, postId: comment.postId }) : null}
          >
            <FontAwesomeIcon className="comment-delete-icon" icon="trash-alt" />
          </button>
        </div>
      </div>
    </div>
  )
}

export default Comment;
